import React, { useState, useEffect } from 'react';
import {
   Box,
   Card,
   CardContent,
   Typography,
   Grid,
   Paper,
   Divider,
   IconButton,
   Badge,
   Tooltip,
   Chip,
   useTheme
} from '@mui/material';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import EventIcon from '@mui/icons-material/Event';
import { usePrayerTimes } from '../../contexts/PrayerTimesContext';

// Calendar constants
const DAYS_OF_WEEK = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
const DAYS_OF_WEEK_SHORT = ['Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa'];

/**
 * HijriCalendar component that displays Hijri (Islamic) calendar information
 */
const HijriCalendar = () => {
   const theme = useTheme();
   const { hijriDate, prayerTimesService } = usePrayerTimes();

   const [currentMonth, setCurrentMonth] = useState(null);
   const [currentYear, setCurrentYear] = useState(null);
   const [calendarData, setCalendarData] = useState([]);
   const [islamicEvents, setIslamicEvents] = useState([]);
   const [todayHijri, setTodayHijri] = useState(null);

   // Initialize with current Hijri date when component mounts
   useEffect(() => {
      if (hijriDate) {
         setCurrentMonth(hijriDate.month.number);
         setCurrentYear(hijriDate.year);
         setTodayHijri(hijriDate);
         loadCalendarData(hijriDate.month.number, hijriDate.year);
         loadIslamicEvents(hijriDate.year);
      }
   }, [hijriDate]);

   // Update the loadCalendarData function
   const loadCalendarData = (month, year) => {
      try {
         // Simple function to calculate days in a Hijri month (fallback)
         const calculateDaysInHijriMonth = (m, y) => {
            // In the Hijri calendar, odd-numbered months typically have 30 days
            // and even-numbered months have 29 days, with some exceptions in leap years
            const isLeapYear = [2, 5, 7, 10, 13, 16, 18, 21, 24, 26, 29].includes(y % 30);
            if (m % 2 === 1) return 30;
            if (m === 12 && isLeapYear) return 30;
            return 29;
         };

         // Use service if available, otherwise calculate locally
         const daysInMonth = prayerTimesService?.getHijriCalendar ?
            prayerTimesService.getHijriCalendar(month, year).daysInMonth :
            calculateDaysInHijriMonth(month, year);

         // Calculate first day of month (this is a simplified approach)
         const firstDayOfMonth = (month + year) % 7;

         const days = [];
         let dayCounter = 1;

         // Create a 6-row calendar (max possible rows needed)
         for (let i = 0; i < 6; i++) {
            const week = [];
            for (let j = 0; j < 7; j++) {
               if ((i === 0 && j < firstDayOfMonth) || dayCounter > daysInMonth) {
                  // Empty cell before start of month or after end of month
                  week.push(null);
               } else {
                  week.push({
                     day: dayCounter,
                     isToday: isToday(dayCounter, month, year),
                     events: getEventsForDay(dayCounter, month, year)
                  });
                  dayCounter++;
               }
            }
            days.push(week);

            // Break if we've reached the end of the month
            if (dayCounter > daysInMonth) break;
         }

         setCalendarData(days);
      } catch (error) {
         console.error('Error loading Hijri calendar data:', error);
      }
   };

   // Update loadIslamicEvents to use our client-side implementation
   const loadIslamicEvents = (year) => {
      try {
         const events = [
            { name: 'Islamic New Year', month: 1, day: 1 },
            { name: 'Day of Ashura', month: 1, day: 10 },
            { name: 'Mawlid al-Nabi', month: 3, day: 12 },
            { name: 'Laylat al-Miraj', month: 7, day: 27 },
            { name: 'Laylat al-Bara\'at', month: 8, day: 15 },
            { name: 'Start of Ramadan', month: 9, day: 1 },
            { name: 'Laylat al-Qadr', month: 9, day: 27 },
            { name: 'Eid al-Fitr', month: 10, day: 1 },
            { name: 'Day of Arafah', month: 12, day: 9 },
            { name: 'Eid al-Adha', month: 12, day: 10 }
         ];
         setIslamicEvents(events);
      } catch (error) {
         console.error('Error loading Islamic events:', error);
      }
   };

   // Check if a day is today in Hijri calendar
   const isToday = (day, month, year) => {
      return hijriDate &&
         hijriDate.day === day &&
         hijriDate.month.number === month &&
         hijriDate.year === parseInt(year);
   };

   // Get events for a specific day
   const getEventsForDay = (day, month, year) => {
      return islamicEvents.filter(event =>
         event.day === day && event.month === month
      );
   };

   // Navigate to previous month
   const goToPreviousMonth = () => {
      let newMonth = currentMonth - 1;
      let newYear = currentYear;

      if (newMonth < 1) {
         newMonth = 12;
         newYear--;
      }

      setCurrentMonth(newMonth);
      setCurrentYear(newYear);
      loadCalendarData(newMonth, newYear);
   };

   // Navigate to next month
   const goToNextMonth = () => {
      let newMonth = currentMonth + 1;
      let newYear = currentYear;

      if (newMonth > 12) {
         newMonth = 1;
         newYear++;
      }

      setCurrentMonth(newMonth);
      setCurrentYear(newYear);
      loadCalendarData(newMonth, newYear);
   };

   // Get month name based on the month number
   const getHijriMonthName = (monthNumber) => {
      const monthNames = [
         'Muharram', 'Safar', 'Rabi al-Awwal', 'Rabi al-Thani',
         'Jumada al-Awwal', 'Jumada al-Thani', 'Rajab', 'Shaban',
         'Ramadan', 'Shawwal', 'Dhu al-Qadah', 'Dhu al-Hijjah'
      ];

      return monthNames[monthNumber - 1];
   };

   // If no Hijri date is available, show loading state
   if (!hijriDate || !currentMonth || !currentYear) {
      return (
         <Card sx={{ mb: 4, borderRadius: 2, boxShadow: 3 }}>
            <CardContent>
               <Typography variant="h6" gutterBottom sx={{ color: 'primary.main' }}>
                  Islamic Calendar
               </Typography>
               <Box sx={{ p: 3, textAlign: 'center' }}>
                  <Typography>Loading Hijri calendar...</Typography>
               </Box>
            </CardContent>
         </Card>
      );
   }

   return (
      <Card sx={{ mb: 4, borderRadius: 2, boxShadow: 3 }}>
         <CardContent>
            <Typography variant="h6" gutterBottom sx={{ color: 'primary.main', fontWeight: 'medium' }}>
               Islamic Calendar
            </Typography>

            {/* Today's Hijri date display */}
            <Box sx={{ mb: 3, textAlign: 'center' }}>
               <Typography variant="h5" gutterBottom sx={{ color: theme.palette.secondary.main, fontWeight: 'bold' }}>
                  {todayHijri.day} {todayHijri.month.en} {todayHijri.year} AH
               </Typography>
               <Typography variant="body2" color="text.secondary">
                  Corresponding to {new Date().toLocaleDateString('en-US', {
                     weekday: 'long',
                     year: 'numeric',
                     month: 'long',
                     day: 'numeric'
                  })}
               </Typography>
            </Box>

            <Divider sx={{ mb: 3 }} />

            {/* Month navigation */}
            <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', mb: 2 }}>
               <IconButton onClick={goToPreviousMonth}>
                  <ChevronLeftIcon />
               </IconButton>

               <Typography variant="h6">
                  {getHijriMonthName(currentMonth)} {currentYear} AH
               </Typography>

               <IconButton onClick={goToNextMonth}>
                  <ChevronRightIcon />
               </IconButton>
            </Box>

            {/* Calendar grid */}
            <Box sx={{ mb: 3 }}>
               <Grid container>
                  {/* Days of week header */}
                  {DAYS_OF_WEEK_SHORT.map((day, index) => (
                     <Grid
                        item
                        key={day}
                        xs={12 / 7}
                        sx={{
                           py: 1,
                           textAlign: 'center',
                           fontWeight: 'medium',
                           bgcolor: index === 5 ? 'rgba(76, 175, 80, 0.1)' : 'transparent' // Highlight Friday
                        }}
                     >
                        <Typography variant="body2" sx={{ fontWeight: index === 5 ? 'bold' : 'inherit' }}>
                           {day}
                        </Typography>
                     </Grid>
                  ))}

                  {/* Calendar days */}
                  {calendarData.map((week, weekIndex) => (
                     <React.Fragment key={`week-${weekIndex}`}>
                        {week.map((day, dayIndex) => (
                           <Grid
                              item
                              xs={12 / 7}
                              key={`day-${weekIndex}-${dayIndex}`}
                              sx={{
                                 height: 50,
                                 p: 0.5,
                                 border: '1px solid',
                                 borderColor: 'divider'
                              }}
                           >
                              {day && (
                                 <Box
                                    sx={{
                                       height: '100%',
                                       p: 0.5,
                                       borderRadius: 1,
                                       bgcolor: day.isToday ? theme.palette.primary.main : 'transparent',
                                       color: day.isToday ? 'white' : 'inherit',
                                       position: 'relative',
                                       display: 'flex',
                                       flexDirection: 'column',
                                       alignItems: 'center'
                                    }}
                                 >
                                    <Typography variant="body2">{day.day}</Typography>
                                    {day.events && day.events.length > 0 && (
                                       <Tooltip title={day.events.map(e => e.name).join(', ')}>
                                          <Box
                                             sx={{
                                                width: '60%',
                                                height: 4,
                                                borderRadius: 2,
                                                bgcolor: day.isToday ? 'rgba(255,255,255,0.7)' : theme.palette.secondary.main,
                                                mt: 'auto'
                                             }}
                                          />
                                       </Tooltip>
                                    )}
                                 </Box>
                              )}
                           </Grid>
                        ))}
                     </React.Fragment>
                  ))}
               </Grid>
            </Box>

            <Divider sx={{ mb: 3 }} />

            {/* Important Islamic dates */}
            <Typography variant="subtitle1" gutterBottom sx={{ fontWeight: 'medium' }}>
               Important Islamic Dates
            </Typography>

            <Box sx={{ mb: 2 }}>
               <Grid container spacing={1}>
                  {islamicEvents.map((event) => (
                     <Grid item key={`${event.month}-${event.day}-${event.name}`}>
                        <Chip
                           icon={<EventIcon />}
                           label={`${event.name} (${event.day} ${getHijriMonthName(event.month)})`}
                           color={event.month === currentMonth ? "primary" : "default"}
                           variant={event.month === currentMonth ? "filled" : "outlined"}
                           size="small"
                           sx={{ mb: 1 }}
                        />
                     </Grid>
                  ))}
               </Grid>
            </Box>
         </CardContent>
      </Card>
   );
};

export default HijriCalendar;