import React, { useState, useEffect } from 'react';
import {
    Container,
    Typography,
    Paper,
    Box,
    Grid,
    Card,
    CardContent,
    Divider,
    CircularProgress,
    Alert,
    Tabs,
    Tab,
    TextField,
    Button,
    IconButton
} from '@mui/material';
import { usePrayerTimes } from '../contexts/PrayerTimesContext';
import prayerTimesService from '../services/prayerTimesService';
import HijriCalendar from '../components/prayer-times/HijriCalendar';
import CalendarMonthIcon from '@mui/icons-material/CalendarMonth';
import DateRangeIcon from '@mui/icons-material/DateRange';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import NavigateBeforeIcon from '@mui/icons-material/NavigateBefore';
import TodayIcon from '@mui/icons-material/Today';
import EventNoteIcon from '@mui/icons-material/EventNote';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { LocalizationProvider, DatePicker } from '@mui/x-date-pickers';

const CalendarPage = () => {
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);
    const [calendar, setCalendar] = useState(null);
    const [month, setMonth] = useState(new Date().getMonth() + 1); // 1-12
    const [year, setYear] = useState(new Date().getFullYear());
    const [tabValue, setTabValue] = useState(0);
    const [viewType, setViewType] = useState('month'); // 'month' or 'year'

    const { params } = usePrayerTimes();

    // Handle tab change
    const handleTabChange = (event, newValue) => {
        setTabValue(newValue);
    };

    const handleMonthChange = (newMonth) => {
        if (newMonth < 1) {
            setYear(year - 1);
            setMonth(12);
        } else if (newMonth > 12) {
            setYear(year + 1);
            setMonth(1);
        } else {
            setMonth(newMonth);
        }
    };

    // Reset to current month
    const handleResetToCurrentDate = () => {
        const now = new Date();
        setMonth(now.getMonth() + 1);
        setYear(now.getFullYear());
    };

    // Fetch calendar data when params, month or year changes
    useEffect(() => {
        const fetchCalendar = async () => {
            if (!params.latitude || !params.longitude) return;

            setLoading(true);
            setError(null);

            try {
                const calendarParams = {
                    latitude: params.latitude,
                    longitude: params.longitude,
                    method: params.method,
                    school: params.school,
                    latitudeAdjustmentMethod: params.latitudeAdjustmentMethod,
                    midnightMode: params.midnightMode
                };

                let response;
                try {
                    if (viewType === 'month') {
                        // Fetch monthly calendar
                        response = await prayerTimesService.getMonthlyPrayerTimes(year, month, calendarParams);
                    } else {
                        // Fetch yearly calendar
                        response = await prayerTimesService.getYearlyPrayerTimes(year, calendarParams);
                    }

                    if (response.code === 200) {
                        if (viewType === 'month') {
                            // Format monthly response
                            const formattedCalendar = {
                                viewType: 'month',
                                months: {
                                    gregorian: {
                                        name: response.data.monthName || new Date(year, month - 1, 1).toLocaleString('default', { month: 'long' }),
                                        number: response.data.month
                                    },
                                    hijri: {
                                        name: "Islamic Calendar",
                                        number: ""
                                    }
                                },
                                days: response.data.days || [],
                                meta: response.data.meta || {}
                            };
                            setCalendar(formattedCalendar);
                        } else {
                            // Format yearly response
                            const formattedCalendar = {
                                viewType: 'year',
                                year: response.data.year,
                                months: response.data.months || [],
                                meta: response.data.meta || {}
                            };
                            setCalendar(formattedCalendar);
                        }
                    } else {
                        console.warn('API returned non-200 status:', response.status);
                        // Set a minimal calendar object to prevent UI errors
                        setCalendar({
                            viewType: viewType,
                            months: {
                                gregorian: {
                                    name: new Date(year, month - 1, 1).toLocaleString('default', { month: 'long' }),
                                    number: month
                                },
                                hijri: {
                                    name: "Calendar View",
                                    number: ""
                                }
                            }
                        });
                    }
                } catch (apiError) {
                    console.error('Calendar API error:', apiError);
                    // Fallback calendar object
                    setCalendar({
                        viewType: viewType,
                        months: {
                            gregorian: {
                                name: new Date(year, month - 1, 1).toLocaleString('default', { month: 'long' }),
                                number: month
                            },
                            hijri: {
                                name: "Calendar View",
                                number: ""
                            }
                        }
                    });
                }
            } catch (err) {
                setError(err.message || 'An error occurred while fetching calendar data');
                console.error(err);
            } finally {
                setLoading(false);
            }
        };

        fetchCalendar();
    }, [params, month, year, viewType]);

    // Render prayer times for a specific day
    const renderDayPrayerTimes = (day) => {
        if (!day.timings) return null;

        return (
            <Grid container alignItems="center">
                <Grid item xs={2}>
                    <Box sx={{ display: 'flex', flexDirection: 'column' }}>
                        <Typography variant="body1" fontWeight="medium">
                            {new Date(day.date).getDate()}
                        </Typography>
                        <Typography variant="caption" color="text.secondary">
                            {day.weekday}
                        </Typography>
                        {day.hijri && (
                            <Typography variant="caption" color="text.secondary">
                                {day.hijri.day} {day.hijri.month?.en}
                            </Typography>
                        )}
                    </Box>
                </Grid>
                <Grid item xs={2}>
                    <Typography variant="body2">{day.timings.Fajr}</Typography>
                </Grid>
                <Grid item xs={2}>
                    <Typography variant="body2">{day.timings.Sunrise}</Typography>
                </Grid>
                <Grid item xs={2}>
                    <Typography variant="body2">{day.timings.Dhuhr}</Typography>
                </Grid>
                <Grid item xs={2}>
                    <Typography variant="body2">{day.timings.Asr}</Typography>
                </Grid>
                <Grid item xs={2}>
                    <Typography variant="body2">{day.timings.Maghrib}</Typography>
                </Grid>
            </Grid>
        );
    };

    // Render monthly calendar view
    const renderMonthlyView = () => {
        if (!calendar || !calendar.days) {
            return (
                <Box sx={{ mt: 4, p: 3, textAlign: 'center', border: '1px dashed', borderColor: 'divider', borderRadius: 2 }}>
                    <Typography variant="h6" gutterBottom>
                        Prayer Times Calendar
                    </Typography>
                    <Typography variant="body1" sx={{ mb: 2 }}>
                        No prayer times data available for the selected month. Please try a different month or check your connection.
                    </Typography>
                </Box>
            );
        }

        const today = new Date().toISOString().split('T')[0];

        return (
            <Box sx={{ mt: 2 }}>
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <Paper sx={{ p: 2, mb: 2, bgcolor: 'primary.main', color: 'primary.contrastText' }}>
                            <Grid container>
                                <Grid item xs={2}>
                                    <Typography variant="subtitle1" fontWeight="bold">Date</Typography>
                                </Grid>
                                <Grid item xs={2}>
                                    <Typography variant="subtitle1" fontWeight="bold">Fajr</Typography>
                                </Grid>
                                <Grid item xs={2}>
                                    <Typography variant="subtitle1" fontWeight="bold">Sunrise</Typography>
                                </Grid>
                                <Grid item xs={2}>
                                    <Typography variant="subtitle1" fontWeight="bold">Dhuhr</Typography>
                                </Grid>
                                <Grid item xs={2}>
                                    <Typography variant="subtitle1" fontWeight="bold">Asr</Typography>
                                </Grid>
                                <Grid item xs={2}>
                                    <Typography variant="subtitle1" fontWeight="bold">Maghrib</Typography>
                                </Grid>
                            </Grid>
                        </Paper>
                    </Grid>

                    {calendar.days.map((day, index) => (
                        <Grid item xs={12} key={day.date || index}>
                            <Paper
                                sx={{
                                    p: 2,
                                    bgcolor: day.date === today ? 'action.selected' : 'background.paper'
                                }}
                                elevation={day.date === today ? 3 : 1}
                            >
                                {renderDayPrayerTimes(day)}
                            </Paper>
                        </Grid>
                    ))}
                </Grid>
            </Box>
        );
    };

    // Update the renderYearlyView function to handle simplified responses
    const renderYearlyView = () => {
        if (!calendar || !calendar.months) {
            return (
                <Box sx={{ mt: 4, p: 3, textAlign: 'center', border: '1px dashed', borderColor: 'divider', borderRadius: 2 }}>
                    <Typography variant="h6" gutterBottom>
                        Yearly Prayer Times Calendar
                    </Typography>
                    <Typography variant="body1" sx={{ mb: 2 }}>
                        No yearly prayer times data available. Please try a different year or check your connection.
                    </Typography>
                </Box>
            );
        }

        return (
            <Box sx={{ mt: 2 }}>
                <Typography variant="h5" gutterBottom sx={{ mb: 3 }}>
                    {year} Prayer Times Overview
                    {calendar.isSimplified && (
                        <Typography component="span" variant="body2" color="text.secondary" sx={{ ml: 2 }}>
                            (Preview mode - showing first day of each month)
                        </Typography>
                    )}
                </Typography>

                {calendar.isSimplified && (
                    <Alert severity="info" sx={{ mt: 1, mb: 3 }}>
                        For performance reasons, the yearly view shows sample prayer times from the first day of each month.
                        For complete daily prayer times, please click "View Details" on any month.
                    </Alert>
                )}

                <Grid container spacing={3}>
                    {Array.isArray(calendar.months) && calendar.months.map((monthData, idx) => (
                        <Grid item xs={12} md={6} lg={4} key={`month-${idx}`}>
                            <Paper sx={{ p: 2, height: '100%' }}>
                                <Typography variant="h6" color="primary" gutterBottom>
                                    {monthData.monthName}
                                </Typography>

                                <Button
                                    variant="outlined"
                                    size="small"
                                    onClick={() => {
                                        setMonth(monthData.month);
                                        setViewType('month');
                                    }}
                                    startIcon={<CalendarMonthIcon />}
                                    sx={{ mb: 2 }}
                                >
                                    View Details
                                </Button>

                                {monthData.error ? (
                                    <Typography variant="body2" color="error">
                                        {monthData.error}
                                    </Typography>
                                ) : (
                                    <>
                                        <Typography variant="body2" color="text.secondary" paragraph>
                                            {monthData.days?.length || 0} days of prayer times available
                                            {monthData.isPreview && " (Preview)"}
                                        </Typography>

                                        {monthData.days && monthData.days.length > 0 && (
                                            <Box sx={{ mt: 1 }}>
                                                <Typography variant="body2" gutterBottom>
                                                    <strong>Sample Prayer Times {monthData.isPreview ? "(1st of month)" : ""}:</strong>
                                                </Typography>
                                                <Box sx={{ pl: 2, borderLeft: '2px solid', borderColor: 'primary.main', ml: 1 }}>
                                                    <Typography variant="caption" display="block">
                                                        Fajr: {monthData.days[0].timings?.Fajr}
                                                    </Typography>
                                                    <Typography variant="caption" display="block">
                                                        Dhuhr: {monthData.days[0].timings?.Dhuhr}
                                                    </Typography>
                                                    <Typography variant="caption" display="block">
                                                        Asr: {monthData.days[0].timings?.Asr}
                                                    </Typography>
                                                    <Typography variant="caption" display="block">
                                                        Maghrib: {monthData.days[0].timings?.Maghrib}
                                                    </Typography>
                                                    <Typography variant="caption" display="block">
                                                        Isha: {monthData.days[0].timings?.Isha}
                                                    </Typography>
                                                </Box>
                                            </Box>
                                        )}
                                    </>
                                )}
                            </Paper>
                        </Grid>
                    ))}
                </Grid>
            </Box>
        );
    };

    if (loading) {
        return (
            <Container maxWidth="lg" sx={{ py: 4, textAlign: 'center' }}>
                <CircularProgress size={60} />
                <Typography variant="h6" sx={{ mt: 2, color: 'text.secondary' }}>
                    Loading calendar data...
                </Typography>
            </Container>
        );
    }

    return (
        <Container maxWidth="lg" sx={{ py: 4 }}>
            <Typography variant="h4" component="h1" gutterBottom sx={{ mb: 4, color: 'primary.main', fontWeight: 500 }}>
                Islamic Calendar
            </Typography>

            {error && (
                <Alert severity="error" sx={{ mb: 3 }}>
                    {error}
                </Alert>
            )}

            {!params.latitude && !params.longitude && (
                <Paper sx={{ p: 4, textAlign: 'center' }}>
                    <Typography variant="h6" color="textSecondary" gutterBottom>
                        Please set your location first
                    </Typography>
                    <Typography variant="body2" color="textSecondary">
                        Go to the Prayer Times page to set your location and calculation method.
                    </Typography>
                </Paper>
            )}

            {params.latitude && params.longitude && (
                <Box sx={{ width: '100%' }}>
                    <Box sx={{ borderBottom: 1, borderColor: 'divider', mb: 3 }}>
                        <Tabs
                            value={tabValue}
                            onChange={handleTabChange}
                            aria-label="calendar tabs"
                            centered
                        >
                            <Tab
                                icon={<DateRangeIcon />}
                                iconPosition="start"
                                label="Hijri Calendar"
                                id="tab-0"
                                aria-controls="tabpanel-0"
                            />
                            <Tab
                                icon={<CalendarMonthIcon />}
                                iconPosition="start"
                                label="Prayer Times Calendar"
                                id="tab-1"
                                aria-controls="tabpanel-1"
                            />
                        </Tabs>
                    </Box>

                    {/* Hijri Calendar Tab */}
                    <Box
                        role="tabpanel"
                        hidden={tabValue !== 0}
                        id="tabpanel-0"
                        aria-labelledby="tab-0"
                    >
                        {tabValue === 0 && <HijriCalendar />}
                    </Box>

                    {/* Prayer Times Calendar Tab */}
                    <Box
                        role="tabpanel"
                        hidden={tabValue !== 1}
                        id="tabpanel-1"
                        aria-labelledby="tab-1"
                    >
                        {tabValue === 1 && (
                            <Box>
                                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
                                    {/* View Type Toggle */}
                                    <Box>
                                        <Button
                                            variant={viewType === 'month' ? 'contained' : 'outlined'}
                                            onClick={() => setViewType('month')}
                                            startIcon={<CalendarMonthIcon />}
                                            sx={{ mr: 1 }}
                                        >
                                            Month
                                        </Button>
                                        <Button
                                            variant={viewType === 'year' ? 'contained' : 'outlined'}
                                            onClick={() => setViewType('year')}
                                            startIcon={<EventNoteIcon />}
                                        >
                                            Year
                                        </Button>
                                    </Box>

                                    {/* Date Navigation and Reset */}
                                    <Box>
                                        <Button
                                            variant="outlined"
                                            startIcon={<TodayIcon />}
                                            onClick={handleResetToCurrentDate}
                                            sx={{ ml: 2 }}
                                        >
                                            Today
                                        </Button>
                                    </Box>
                                </Box>

                                {/* Month/Year Selection Controls */}
                                <Paper sx={{ p: 3, mb: 4, bgcolor: 'background.default' }}>
                                    <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                        <Box sx={{ display: 'flex', alignItems: 'center' }}>
                                            {viewType === 'month' && (
                                                <>
                                                    <IconButton
                                                        onClick={() => handleMonthChange(month - 1)}
                                                        aria-label="Previous month"
                                                    >
                                                        <NavigateBeforeIcon />
                                                    </IconButton>

                                                    <LocalizationProvider dateAdapter={AdapterDateFns}>
                                                        <DatePicker
                                                            views={['year', 'month']}
                                                            label="Year and Month"
                                                            minDate={new Date('2000-01-01')}
                                                            maxDate={new Date('2050-12-31')}
                                                            value={new Date(year, month - 1)}
                                                            onChange={(newDate) => {
                                                                if (newDate) {
                                                                    setYear(newDate.getFullYear());
                                                                    setMonth(newDate.getMonth() + 1);
                                                                }
                                                            }}
                                                            renderInput={(params) => <TextField {...params} variant="standard" helperText={null} />}
                                                            sx={{ mx: 2, width: 170 }}
                                                        />
                                                    </LocalizationProvider>

                                                    <IconButton
                                                        onClick={() => handleMonthChange(month + 1)}
                                                        aria-label="Next month"
                                                    >
                                                        <NavigateNextIcon />
                                                    </IconButton>
                                                </>
                                            )}

                                            {viewType === 'year' && (
                                                <>
                                                    <IconButton
                                                        onClick={() => setYear(year - 1)}
                                                        aria-label="Previous year"
                                                    >
                                                        <NavigateBeforeIcon />
                                                    </IconButton>

                                                    <LocalizationProvider dateAdapter={AdapterDateFns}>
                                                        <DatePicker
                                                            views={['year']}
                                                            label="Year"
                                                            minDate={new Date('2000-01-01')}
                                                            maxDate={new Date('2050-12-31')}
                                                            value={new Date(year, 0)}
                                                            onChange={(newDate) => {
                                                                if (newDate) {
                                                                    setYear(newDate.getFullYear());
                                                                }
                                                            }}
                                                            renderInput={(params) => <TextField {...params} variant="standard" helperText={null} />}
                                                            sx={{ mx: 2, width: 170 }}
                                                        />
                                                    </LocalizationProvider>

                                                    <IconButton
                                                        onClick={() => setYear(year + 1)}
                                                        aria-label="Next year"
                                                    >
                                                        <NavigateNextIcon />
                                                    </IconButton>
                                                </>
                                            )}
                                        </Box>

                                        {/* Calendar Info */}
                                        <Box sx={{ textAlign: 'right' }}>
                                            <Typography variant="h5" gutterBottom>
                                                {viewType === 'month'
                                                    ? `${calendar?.months?.gregorian?.name || new Date(year, month - 1).toLocaleString('default', { month: 'long' })} ${year}`
                                                    : `${year} Prayer Times Calendar`
                                                }
                                            </Typography>
                                            {calendar?.meta && (
                                                <Typography variant="body2" color="text.secondary">
                                                    Method: {calendar.meta.method?.name || 'ISNA'}  School: {calendar.meta.school || 'Standard'}
                                                </Typography>
                                            )}
                                        </Box>
                                    </Box>
                                </Paper>

                                {/* Calendar Content */}
                                {viewType === 'month' ? renderMonthlyView() : renderYearlyView()}
                            </Box>
                        )}
                    </Box>
                </Box>
            )}
        </Container>
    );
};

export default CalendarPage;